.cover {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0; }

.nav li {
  display: inline; }
  .nav li a {
    display: inline-block; }

.hide-text {
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap; }

.clearfix {
  *zoom: 1; }
  .clearfix:before,
  .clearfix:after {
    content: '';
    display: table; }
  .clearfix:after {
    clear: both; }

* {
  box-sizing: border-box; }

img {
  max-width: 100%;
  height: auto; }

.wrapper {
  max-width: 1180px;
  margin: 0 auto; }

.grids {
  overflow: hidden;
  clear: both;
  margin-left: -3em; }
  .grids > * {
    width: 100%;
    float: left;
    padding-left: 3em; }
    .grids > * *:first-child {
      margin-top: 0; }

.half-gutter {
  margin-left: -1.5em; }
  .half-gutter > * {
    padding-left: 1.5em; }

.alignnone {
  margin: 0.25em 1em 1em 0; }

.aligncenter {
  display: block;
  margin: 0.25em auto 0.25em auto; }

.alignleft {
  float: left;
  margin: 0.25em 1em 1em 0; }

.alignright {
  float: right;
  margin: 0.25em 0 1em 1em; }

.wp-caption {
  max-width: 96%;
  padding: 0.25 0.25em 0.75em;
  background: #fff;
  border: 1px solid #f0f0f0;
  text-align: center; }
  .wp-caption.alignnone {
    margin: 0.25em 1em 1em 0; }
  .wp-caption.alignleft {
    margin: 0.25em 1em 1em 0; }
  .wp-caption.alignright {
    margin: 0.25em 0 1em 1em; }
  .wp-caption img {
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
    border: 0 none; }
  .wp-caption p.wp-caption-text {
    margin: 0;
    padding: 0 0.25em 0.25em;
    font-size: 0.75em;
    line-height: 1em; }

